// Базовые цвета https://colorscheme.ru/color-converter.html

$black:                       hsl(0, 0%, 0%);
$gray-darkest:                hsl(0, 0%, 10%);
$gray-darker:                 hsl(0, 0%, 20%);
$gray-darken:                 hsl(0, 0%, 30%);
$gray-dark:                   hsl(0, 0%, 40%);
$gray:                        hsl(0, 0%, 50%);
$gray-light:                  hsl(0, 0%, 60%);
$gray-lighten:                hsl(0, 0%, 70%);
$gray-lighter:                hsl(0, 0%, 80%);
$gray-lightest:               hsl(0, 0%, 90%);
$white:                       hsl(0, 0%, 100%);

$color-success:               hsl(120, 39%, 54%);
$color-danger:                hsl(2, 64%, 58%);
$color-warning:               hsl(35, 84%, 62%);

//$color-vk:                    #4a76a8;
//$color-fb:                    #4267b2;
//$color-tw:                    #40A2F5;
//$color-gh:                    #24292e;


// Семантические цвета

$main-color:                  #0270CA;
$color-main:                  $main-color;
$secondary-color:             #FF6100;

$text-color:                  #575B66;
$title-color:                 #393939;
$text-muted:                  #BDBDBD;

$body-bg:                     #ffffff;

$link-color:                  $main-color;
$link-color--hover:           #4240CF;

$border-color:                $text-muted;
$divider-color:               $main-color;
// Базовая типографика

$font-size:                   16px;

$font-size--h1:               38px;
$font-size--h2:               32px;
$font-size--h3:               28px;
$font-size--h4:               24px;
$font-size--h5:               20px;
$font-size--h6:               16px;

$line-height:                 1.5;

$typo-margin-vertical:        1em;

$font-family:                 'Exo 2', 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
//$font-family--headings:      -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
$font-family--monospace:      SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;


// Ширины

$screen-xs:                   0;
$screen-sm:                   480px;
$screen-md:                   768px;
$screen-lg:                   992px;
$screen-xl:                   1280px;
$screen-xxl:                  1440px;

$container-sm:                100%;
$container-md:                100%;
$container-lg:                100%;
$container-xl:                $screen-xl - 20;
$container-xxl:               $screen-xxl - 20;


// Модульная сетка

$grid-columns:                12;
$grid-gutter-width:           24px;



// Разное

$field-padding-vertical:      5px;
$field-padding-horizontal:    12px;

$border-radius:               3px;
$opacity:                     0.7;
$shadow:                      0 4px 2px -2px rgba(0, 0, 0, 0.3);
$transition-time:             0.2s;

// BS4 vars for mixins
$enable-transitions:                          true !default;
$enable-prefers-reduced-motion-media-query:   true !default;
$transition-base:                             all 0.2s ease-in-out !default;
$transition-fade:                             opacity 0.15s linear !default;
$transition-collapse:                         height 0.2s ease !default;

$menu-desktop-width:          $screen-xl;
$toggler-part-height:         3px;
$toggler-part-bg-color:       #000;

