  figure,
  figure +
  figure {
    margin-top: 0.5em; }

p {
  hyphens: auto;
  hyphenate-limit-chars: 6 3 3;
  hyphenate-limit-lines: 2;
  hyphenate-limit-last: always;
  hyphenate-limit-zone: 8%; }

address {
  font-style: normal; }

ul,
ol {
  padding-left: 1.3em; }

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0;
  margin-bottom: 0; }

li {
  line-height: inherit; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  margin-top: 1em;
  margin-bottom: 1em;
  border: 0;
  border-top: 1px solid #BDBDBD; }

.small,
small {
  font-size: 12px; }

sub,
sup {
  font-size: 70%; }

b,
strong {
  font-weight: bolder; }

abbr[title] {
  text-decoration: underline dotted; }

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0; }

dt {
  font-weight: 700; }

dd {
  margin-left: 0; }
  @media (min-width: 480px) {
    dd {
      margin-left: 1.5rem; } }
  dd + dt {
    margin-top: 0.5em; }

code {
  padding: 0.06em 0.3em;
  color: #575B66;
  background-color: rgba(0, 0, 0, 0.08);
  border-radius: 3px; }

kbd {
  padding: 0.06em 0.3em;
  color: #575B66;
  background-color: #e6e6e6;
  border-radius: 3px; }
  kbd kbd {
    padding: 0; }

pre {
  display: block;
  width: 100%;
  margin-bottom: 0.5em;
  overflow-x: auto;
  tab-size: 2;
  background-color: rgba(0, 0, 0, 0.08); }
  pre code {
    background: transparent; }

ol {
  list-style: none;
  counter-reset: something;
  padding-left: 32px; }
  ol li {
    counter-increment: something; }
    ol li::before {
      content: counters(something, ".") ". "; }

a {
  text-decoration-skip-ink: inherit; }

